/*Common styles*/
/*Common styles*/
body {
    font-family: 'Lato', sans-serif;
	color:#000;
	width:100%;
	height:100%;
	margin:0;
	padding:0;
	background:#fff;
}

img {
    /*max-height:100%;*/
    max-width:100%;
}
section {
    position:relative;
    padding:0;
    background:#eceae6;
    color:#FFF;
    text-align:center;
}

ul, ol{padding:0;margin:0;}

a{transition:all 0.25s ease-in 0s; outline:0 none; cursor:pointer; text-decoration:none; opacity:1}
a:focus{outline:none; background:none;}
:hover{transition:all 0.25s ease-in 0s;}
.clear{clear:both;}

.pos{position:relative;}
.container , .pos{ padding-right: 15px; padding-left: 15px;margin-right: auto; margin-left: auto;}
@media (min-width: 768px) {
.container, .pos { width: 730px;}
}
@media (min-width: 992px) {
.container, .pos {width: 970px;}
}
@media (min-width: 1200px) {
.container, .pos {width: 1170px;}
}
/*Common styles end*/

/*Tags background*/
.top{background:#f62e00;}
.conutry{background:#0092ee;}
.sports{background:#649e30;}
.politics{background:#c764e8;}
.editorial{background:#0056bd;}
.crime{background:#306bbc;}

/* col styling*/
.col-6{width:50%; float:left; }
.col-3{width:25%; float:left;}
.col-9{width:75%; float:left;}
.desk-share{margin: 10px 0 0 -50px;}

/* Homepage */
/* advertisment box*/
header .adv-box{width:60%; margin:0 auto; padding:15px 0; }
header hr.line{border-top: 0.5px solid rgb(210, 210, 210);}
/* advertisment box end*/

/*logo*/
header .brand-info{border:0px solid red; position:relative;}
header .logo{width:100%; float:left;}
/*logo end*/

/* right side social links */
header .right-links{border:0px solid blue; width:65%;padding:13px 0;float:right;}
header .socials{border:0px solid red;float:right; }
header .socials li{list-style:none; display:inline; text-align:left; font:400 13px/18px 'Work Sans', sans-serif; color:#8a8a8a; padding:0 6px;}
header .socials li a{padding:0 8px 0 4px;}
header .socials li div{display:inline; padding: 0 8px 0 4px;}
header .socials li a .fa{font-size:20px;}
header .socials li a .fa-facebook{color:#3b5989;}
header .socials li a .fa-twitter{color:#00acee;}
header .socials li a .fa-youtube-play{color:#e02f2f;}
header .pie{margin:0px 8px 0 0; position:relative; top:6px;}
header .deals{padding: 2px 5px !important; margin:0 0px 0 0; background:#ee522e; color:#fff;-webkit-border-radius: 3px;-moz-border-radius: 3px;border-radius: 3px;position:relative; top:-2px; font:500 12px/18px 'Work Sans', sans-serif;}
header .deals:hover{background:#e6694d;}
header .icon{position:relative; top:5px;}
header .paper{width:26px; position:relative; top:5px;}
header .pen{width:26px; position:relative; top:5px;}
header .search-b{width:27px; position:relative; top:3px;}
header .bars{width:26px; position:relative; top:4px;}
header .date{width:250px; text-align:center;float: left;}
header .date h4{font:bold 13px/18px 'PT Serif', serif;  color:#1a1a1a; margin:-1px 0 4px 0;}

header .menu-tab{display:none;border-left:1px solid #f2f2f2; width:16%; height:100%; position:fixed;  top: 0; right: 0; background: #fff; z-index: 9999999; overflow-y:scroll;text-align: left;}
.close-icon{width: 23px;  position: absolute;  top: 5px; right: 4px;}
/* width */::-webkit-scrollbar { width: 3px;}
/* Track */::-webkit-scrollbar-track { background: #f1f1f1; }
/* Handle */::-webkit-scrollbar-thumb { background: #ccc; }
/* Handle on hover */::-webkit-scrollbar-thumb:hover { background: #555; }
header .menu-tab .tab-logo{width:60%; margin:0 auto; padding:5px 0 10px 0; }
header .menu-tab ul {border-top:1px solid #f2f2f2; }
header .menu-tab ul li{position:relative;text-align:left;list-style:none; letter-spacing:0.6px; display:block; padding:10px 15px; font:800 13px/18px 'Work Sans', sans-serif; border-bottom:1px solid #f2f2f2;}
header .menu-tab ul li:hover{cursor:pointer;}
header .menu-tab ul a{font:800 13px/18px 'Work Sans', sans-serif; color:#1a1a1a;}
header .menu-tab ul li.has-dropdown{padding:10px 0;}
header .menu-tab ul li.has-dropdown span{padding:0 15px;}
header .menu-tab ul li ul.dropdown{margin-top:10px; display:none;}
header .menu-tab ul li ul.dropdown li{padding-left:25px;}
header .menu-tab ul li ul.dropdown li:hover{background:#f2f2f2;}
header .menu-tab ul li ul li:last-child{border-bottom:none; padding-bottom:0;}
header .menu-tab .fa-plus, header .menu-tab .fa-minus{position:absolute;position: absolute; color:#0f9fdc; right:15px;font-size: 9px;margin: 6px 0 0 0;}


header .search-btn, header .menu-btn{display:none;}
header .search-box{display:none; position: absolute; z-index:9999; right:50px;width:25%; /*left:0; margin:0 auto;*/ background: #fff;top: 73px;padding: 10px;-webkit-border-radius: 3px;-moz-border-radius: 3px;border-radius: 3px; 
-webkit-box-shadow: 0px 20px 24px -5px rgba(0,0,0,0.25);
-moz-box-shadow: 0px 20px 24px -5px rgba(0,0,0,0.25);
box-shadow: 0px 20px 24px -5px rgba(0,0,0,0.25);}
header .search-box .txtsearch{width:87%; float:left; padding:8px 10px; border:1px solid #d9d9d9; }
header .search-box .txtsearch:focus, header .search-box .txtsearch:hover{outline:none;} 
header .search-box .sub-btn{border:1px solid #0f9fdc; font-size:18px; padding:5px 0; width:13%; background:#0f9fdc; color:#fff;}
.fa-caret-up{float: right; margin: -24px 5px 0 0px; color:#fff; font-size:20px;}

/* right side social links end*/

/* menu*/
header .menu{background:#eceae6; padding:0px 0;}
header .menu ul li{list-style:none; letter-spacing:0.6px; display:inline-block; padding:15px 12px; font:800 13px/18px 'Work Sans', sans-serif;}
header .menu ul li a{font:800 13px/18px 'Work Sans', sans-serif; color:#1a1a1a;}
header .menu ul li a .fa.fa-chevron-down{margin:0px 0 0 5px; font-size:9px; position:relative; top:-2px; right:0;}
header .menu ul li a:hover{color:#01aef0;}
header .menu ul li a:hover .fa.fa-chevron-down{color:#1a1a1a;}
header .menu ul li.has-list{padding:10px 10px; position:relative;}
header .menu ul li ul.list{ display:none;position:absolute; left:-10px; top:40px;z-index:999; width:160px; background:#fff;
-webkit-box-shadow: 0px 20px 24px -5px rgba(0,0,0,0.50); -moz-box-shadow: 0px 20px 24px -5px rgba(0,0,0,0.50); box-shadow: 0px 20px 24px -5px rgba(0,0,0,0.50); }
header .menu ul li ul.list li{padding:8px 10px; border-bottom:1px solid #e6e6e6; width: calc(160px - 20px);}
header .menu ul li ul.list li:last-child{border-bottom:none;}
header .menu ul li ul.list li a{font:600 12px/16px 'Work Sans', sans-serif; color:#000;}
header .menu ul li ul.list li:hover{background:#e6e6e6;}

.active{color:#01aef0 !important;}

.icon-bar{height:1px; background:#000; width:27px;margin:0px 0 6px 0;}
.icon-bar:last-child{margin:0 0 0 0;}
