/* Scroll to top btn*/
.scrolltop {
	display:none;
	width:100%;
	margin:0 auto;
	position:fixed;
	bottom:20px;
	right:10px;	
}
.scroll {
	position:absolute;
	right:20px;
	bottom:20px;
	background:#b2b2b2;
	background:#fff;
	padding:6px 10px;
	text-align: center;
	margin: 0 0 0 0;
	cursor:pointer;
	-webkit-box-shadow: 0px 0px 27px -3px rgba(0,0,0,0.40);
    -moz-box-shadow: 0px 0px 27px -3px rgba(0,0,0,0.40);
    box-shadow: 0px 0px 27px -3px rgba(0,0,0,0.40);
	transition: 0.5s;
	-moz-transition: 0.5s;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;	
	
}
.scroll:hover {
	background:#0f9fdc;
	color:#fff;
	transition: 0.5s;
	-moz-transition: 0.5s;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s; 		
}
.scroll:hover .fa {
	padding-top:-10px;
	color:#fff;
}
.scroll .fa {
	font-size:26px;
	margin-top:-2px;
	margin-left:1px;
	transition: 0.5s;
	-moz-transition: 0.5s;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s; 	
}

